* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}
a:visited {
  color: black;
}

.App {
  margin: 0 auto;
  text-align: center;
  position: relative;
}

ul {
  list-style: none;
}

.mainLogo {
  text-align: left;
  position: relative;
  left: 10%;
  margin: 30px;
}
.mainLogo > h1 {
  font-size: 40px;
}

.appMenu {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  font-size: 25px;
  height: 60px;
  background-color: beige;
}
.appMenu > li > a {
  font-weight: 700;
}
.homeMenu {
  position: fixed;
  top: 30%;
  border: 2px solid rgb(226, 226, 205);
  background-color: beige;
  border-radius: 40px;
  width: 150px;
  height: 250px;
  font-size: 17px;
  line-height: 28px;
  padding-top: 20px;
}
.homeMenu > li:hover {
  transition: all 0.1s;
  transform: scale(1.1);
  background-color: rgb(196, 196, 145);
  border-radius: 40px;
  width: 140px;
  z-index: 10;
}
.home_title {
  display: inline-block;
  width: 100px;
  border-bottom: 2px solid rgb(226, 226, 205);
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 10px;
}

/* RecommendProduct */

.homeReProductSection {
  position: relative;
  margin: 0px auto;
  padding: 100px 0;
  width: 75%;
}

.homeReProduct {
  display: inline-block;
  width: 300px;
  height: 400px;
  margin: 20px;
}
.homeReSup {
  text-align: center;
  height: 285px;
}

.homeReProductImg {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.homeTitle {
  padding: 30px;
}
.homeTitle > h2 {
  padding: 20px;
}
.homeDesc {
  font-size: 20px;
  padding: 10px;
}

/* EventProduct */

.homeEventProductSection {
  position: relative;
  margin: 0px auto;
  padding: 100px 0;
  width: 75%;
}
.homeEventProduct {
  display: inline-block;
  width: 400px;
  height: 500px;
  margin: 20px;
}
.homeEventSup {
  height: 310px;
  margin-bottom: 25px;
}
.homeEventProductImg {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.eventId1 {
  height: 100%;
  background-color: rgb(245, 245, 245);
}
.eventId2 {
  height: 100%;
  background-color: rgb(177, 218, 242);
}
.eventId3 {
  height: 100%;
  background-color: rgb(164, 178, 163);
}
.homeEventDesc > h3 {
  margin-bottom: 10px;
}

/* Furniture */

.furnitureProduct {
  display: inline-block;
  width: 530px;
  height: 400px;
  margin: 10px;
}

/* CustomerSupport */

.supportTitle {
  padding-top: 50px;
  width: 80%;
  position: relative;
  margin: 0px auto;
}
.supportTitle > h3 {
  padding: 20px;
  font-size: 25px;
}
.supportTitle > p {
  padding: 10px;
  font-size: 18px;
  font-weight: 600;
}
.serviceContent {
  display: inline-block;
  width: 400px;
  height: 600px;
  margin: 20px;
}
.serviceImg {
  width: 400;
  height: 400;
}
.serviceImg > img {
  width: 100%;
  height: 100%;
}
.supportSub {
  margin-bottom: 20px;
}
.serviceWriting {
  margin: auto;
  width: 350px;
  height: 100px;
}
.serviceWriting > h3 {
  padding: 10px;
}
.serviceWriting > p {
  padding: 10px;
}

/* faq */

.faqContentSection {
  display: flex;
  padding: 10px;
  justify-content: space-around;
  font-size: 15px;
}
.faqContent {
  display: inline-block;
  width: 250px;
  height: 300px;
  margin: 10px;
  background-color: rgb(245, 245, 245);
}
.faqContent h3{
  height: 90px;
  padding: 10px;
  margin-bottom: 30px;
}
.faqContent p{
  height: 180px;
  align-items: flex-end;
}